import {
    Button, Flex, Input, Text,
} from 'shared/ui';
import { Logo } from 'shared/icons';
import { useState } from 'react';
import emailjs from 'emailjs-com';
import styles from './feedback.module.scss';

export const Feedback = () => {
    const [formData, setFormData] = useState({
        name: '',
        email: '',
        message: '',
    });

    const [isSubmitted, setIsSubmitted] = useState(false);
    const [error, setError] = useState<string | null>(null);

    // –û–±—Ä–∞–±–æ—Ç—á–∏–∫ –∏–∑–º–µ–Ω–µ–Ω–∏—è –ø–æ–ª–µ–π
    const handleChange = (e: any) => {
        setFormData({
            ...formData,
            [e.target.name]: e.target.value,
        });
    };

    // –û–±—Ä–∞–±–æ—Ç—á–∏–∫ –æ—Ç–ø—Ä–∞–≤–∫–∏ —Ñ–æ—Ä–º—ã
    const handleSubmit = (e: any) => {
        e.preventDefault();

        // –ü—Ä–æ–≤–µ—Ä–∫–∞ –¥–∞–Ω–Ω—ã—Ö
        if (!formData.name || !formData.email || !formData.message) {
            setError('–ü–æ–∂–∞–ª—É–π—Å—Ç–∞, –∑–∞–ø–æ–ª–Ω–∏—Ç–µ –≤—Å–µ –ø–æ–ª—è.');
            return;
        }

        // –ù–∞—Å—Ç—Ä–æ–π–∫–∏ –¥–ª—è EmailJS
        const serviceID = 'service_gfv3msw';
        const templateID = 'template_nxaeajn';
        const userID = 'i_vaF6jJ835T8EyWA';

        // –û—Ç–ø—Ä–∞–≤–∫–∞ —á–µ—Ä–µ–∑ EmailJS
        emailjs.send(serviceID, templateID, formData, userID)
            .then((response) => {
                console.log('SUCCESS!', response.status, response.text);
                setIsSubmitted(true);
                setError(null); // –û—á–∏—â–∞–µ–º –æ—à–∏–±–∫—É, –µ—Å–ª–∏ –±—ã–ª–∞
            }, (err) => {
                console.error('FAILED...', err);
                setError('–ü—Ä–æ–∏–∑–æ—à–ª–∞ –æ—à–∏–±–∫–∞ –ø—Ä–∏ –æ—Ç–ø—Ä–∞–≤–∫–µ —Å–æ–æ–±—â–µ–Ω–∏—è.');
            });
    };

    return (
        <div className={styles.wrapper}>
            <div className={styles.widget}>
                <Flex align="start" direction="vertical" gap={32}>
                    <Text color="white" size="xl2">–û—Å—Ç–∞–ª–∏—Å—å –≤–æ–ø—Ä–æ—Å—ã?</Text>
                    <Text color="white">
                        –ù–∞–ø–∏—à–∏—Ç–µ –≤–∞—à –≤–æ–ø—Ä–æ—Å –Ω–∞ –ø–æ—á—Ç—É –ú–æ—Å–∫–æ–≤—Å–∫–æ–≥–æ
                        –ü–æ–ª–∏—Ç–µ—Ö–∞ –∏ –º—ã —Å–≤—è–∂–µ–º—Å—è —Å –≤–∞–º–∏ –≤ –∫—Ä–∞—Ç—á–∞–π—à–∏–µ —Å—Ä–æ–∫–∏!
                    </Text>
                </Flex>
                <form className={styles.form} onSubmit={handleSubmit}>
                    <Input
                        variant="outlined"
                        name="name"
                        placeholder="–§–ò–û"
                        value={formData.name}
                        onChange={handleChange}
                    />
                    <Input
                        variant="outlined"
                        type="email"
                        name="email"
                        placeholder="–≠–ª. –ø–æ—á—Ç–∞"
                        value={formData.email}
                        onChange={handleChange}
                    />
                    <Input
                        variant="outlined"
                        name="message"
                        placeholder="–í–∞—à –≤–æ–ø—Ä–æ—Å"
                        value={formData.message}
                        onChange={handleChange}
                    />
                    {error && <Text color="red">{error}</Text>}
                    {isSubmitted && <Text color="green">–°–æ–æ–±—â–µ–Ω–∏–µ —É—Å–ø–µ—à–Ω–æ –æ—Ç–ø—Ä–∞–≤–ª–µ–Ω–æ!</Text>}
                    <Button type="submit" size="l" color="white">
                        <Text color="blue" size="m" weight="semi">
                            –û—Ç–ø—Ä–∞–≤–∏—Ç—å
                        </Text>
                    </Button>
                </form>
            </div>
            <div className={styles.image}>
                <img
                    src={`${process.env.PUBLIC_URL}/assets/email.png`}
                    alt="email"
                />
            </div>
        </div>
    );
};
